import React from 'react';
import {css} from '@emotion/core';
import {withTheme} from 'emotion-theming';

const background = 'white';

const active = theme => css`
  background-color: ${background};
  color: ${theme.colors.primary};
`;

export default withTheme(({value, onChange, theme}) => {
  const ref = React.useRef(null);

  return (
    <input
      ref={ref}
      css={css`
        margin: 0;
        padding: 0 0.8em;
        background-color: transparent;
        color: white;
        transition: background-color 0.5s;
        border: 1px solid ${theme.colors.palette[2]};
        border-radius: 4px;
        &:focus,
        &:hover {
          outline: none;
          ${active(theme)}
        }
        ${value && active(theme)}
      `}
      type="search"
      name="query"
      placeholder="Search"
      autoComplete="off"
      autoCorrect="off"
      autoCapitalize="off"
      spellCheck="false"
      value={value}
      onChange={event => onChange(event.target.value)}
      onFocus={() => setTimeout(() => ref.current && ref.current.select())}
    />
  );
});
