import idx from 'idx';
import lunr from 'lunr';

export default async function search(query) {
  const {index, store} =
    idx(await idx(window, _ => _.__LUNR__.__loaded), _ => _.en) || {};
  if (!query || !index || !store) return [];

  const results = index.query(q => {
    q.term(lunr.tokenizer(query), {boost: 100, usePipeline: true});
    q.term(lunr.tokenizer(query), {
      boost: 10,
      usePipeline: false,
      wildcard: lunr.Query.wildcard.TRAILING,
    });
    if (query.length > 3) {
      q.term(lunr.tokenizer(query), {boost: 1, editDistance: 1});
    } else {
      q.term(lunr.tokenizer(query), {boost: 1});
    }
  });

  return results.map(({ref}) => ({ref, ...store[ref]}));
}
